"use client"

import { useEffect, useState } from "react"
import { createClient } from "@/lib/supabase/client"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Users, Star } from "lucide-react"
import Link from "next/link"

interface Student {
  id: string
  full_name: string
  email: string
  total_points: number
  current_level: number
  overall_accuracy: number
}

export function StudentsList() {
  const [students, setStudents] = useState<Student[]>([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const fetchStudents = async () => {
      try {
        setIsLoading(true)
        const supabase = createClient()

        // ONE QUERY â€” profiles + student_profiles JOIN
        const { data, error } = await supabase
          .from("profiles")
          .select(`
            id,
            full_name,
            email,
            student_profiles (
              total_points,
              current_level,
              overall_accuracy
            )
          `)
          .eq("role", "student")
          .order("full_name", { ascending: true })

        if (error) throw error

        const formatted = data.map((item: any) => ({
          id: item.id,
          full_name: item.full_name,
          email: item.email,
          total_points: item.student_profiles?.total_points ?? 0,
          current_level: item.student_profiles?.current_level ?? 1,
          overall_accuracy: item.student_profiles?.overall_accuracy ?? 0,
        }))

        setStudents(formatted)
      } catch (error) {
        console.error("Error loading students:", error)
      } finally {
        setIsLoading(false)
      }
    }

    fetchStudents()
  }, [])

  return (
    <div className="p-8 space-y-6">
      <div>
        <h2 className="text-3xl font-bold tracking-tight flex items-center gap-2">
          <Users className="w-8 h-8" />
          All Students
        </h2>
        <p className="text-muted-foreground mt-2">Manage and track student performance</p>
      </div>

      <Card className="border-0 shadow-sm">
        <CardHeader>
          <CardTitle>Students</CardTitle>
          <CardDescription>Total: {students.length} students enrolled</CardDescription>
        </CardHeader>

        <CardContent className="space-y-4">
          {isLoading ? (
            <div className="text-center py-8 text-muted-foreground">Loading students...</div>
          ) : (
            <div className="border border-border rounded-lg overflow-hidden">
              <Table>
                <TableHeader>
                  <TableRow className="bg-secondary/50 hover:bg-secondary/50">
                    <TableHead>Name</TableHead>
                    <TableHead>Email</TableHead>
                    <TableHead>Level</TableHead>
                    <TableHead>Points</TableHead>
                    <TableHead>Accuracy</TableHead>
                    <TableHead className="text-right">Actions</TableHead>
                  </TableRow>
                </TableHeader>

                <TableBody>
                  {students.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={6} className="text-center py-8 text-muted-foreground">
                        No students found
                      </TableCell>
                    </TableRow>
                  ) : (
                    students.map((student) => (
                      <TableRow
                        key={student.id}
                        className="hover:bg-secondary/50 transition-colors"
                      >
                        <TableCell className="font-medium">{student.full_name}</TableCell>
                        <TableCell className="text-sm text-muted-foreground">
                          {student.email}
                        </TableCell>

                        <TableCell>
                          <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-primary/20 text-primary">
                            Level {student.current_level}
                          </span>
                        </TableCell>

                        <TableCell className="font-medium">{student.total_points}</TableCell>

                        <TableCell>
                          <div className="flex items-center gap-1">
                            <span className="text-sm font-medium">
                              {student.overall_accuracy.toFixed(1)}%
                            </span>
                            <Star className="w-3 h-3 text-accent fill-accent" />
                          </div>
                        </TableCell>

                        <TableCell className="text-right">
                          <Link href={`/mentor/student/${student.id}`}>
                            <Button size="sm" variant="outline">
                              View Details
                            </Button>
                          </Link>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
